@import "~bootstrap/dist/css/bootstrap.css";
@import "~font-awesome/css/font-awesome.css";
@import "~devicon/devicon-colors.css";
@import "~devicon/devicon.css";
@import url("https://fonts.googleapis.com/css?family=Roboto|Roboto+Mono");

$primaryColor: #2162FF;
$primaryColorLight: #768FFF;
$primaryColorDark: #0039cb;
$accentColor: #76FF03;
$accentColorLight: #B0FF57;
$accentColorDark: #32CB00;
$textColorOnPrimary: #FFFFFF;
$textColorOnAccent: #000000;
$primaryTextColor_dark: rgba(255,255,255,1.0);
$primaryTextColor_light: rgba(0,0,0,0.87);
$secondaryTextColor_dark: rgba(255,255,255,0.7);
$secondaryTextColor_light: rgba(0,0,0,0.54);
$hintTextColor_dark: rgba(255,255,255,0.5);
$hintTextColor_light: rgba(0,0,0,0.38);
$background-color: #FAFAFA;
$background-color-dark: #121212;
$surface-color-dark: #1F1F1F;
$theme-color: #212121;
$theme-color_dark: #000000;
$font-stack: Roboto, Arial, Helvetica, sans-serif;

body {
    background: $background-color;
    font-family: $font-stack;
    color:#000000;
    padding-top: 70px;
    @media(prefers-color-scheme: dark) {
        background: $background-color-dark;
        color: #FFFFFF;
    }
}

h1  {
    font-family: $font-stack;
    small{
        color: $secondaryTextColor_light;			
    }
}

h2.a    {
    font-family: $font-stack;
}

/*Links*/
a {
    &:link { /*Unvisited Link*/
        color:#2196f3;
        background-color:transparent;
        text-decoration:none;
    }
    &:visited { /*Visited Link*/
        color:#0a6ebd;
        background-color:transparent;
        text-decoration:none;
    }
    &:hover	{ /*Mouse over link*/
        color:#0a6ebd; 
        background-color:transparent; 
        text-decoration:underline;
    }
    &:active { /*Selected Link*/
        color:#0a6ebd;
        background-color:transparent; 
        text-decoration:underline;
    }
}

.navbar	{	
    border-radius: 0px 0px 0px 0px;
}

.navbar-default	{
    background-color:$primaryColor;
    border: none;
    border-radius: 0px 0px 0px 0px;
    -webkit-box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2);
    .navbar-nav {
        >li {
            >a {
                color: $textColorOnPrimary;
                -webkit-transition: color 0.275s, background-color 0.275s;
                transition: color 0.275s, background-color 0.275s;
                &:focus, &:hover{
                    color: $textColorOnPrimary;
                    background-color: $primaryColorDark;
                }
            }
        }
        >.active, >.open {
            >a, >a:focus, >a:hover {
                color: $textColorOnPrimary;
                background-color: $primaryColorDark;
            }
        }
    }
    .navbar-collapse {
        border-color: $primaryColor;
    }
    .navbar-toggle {
        border: none;
        .icon-bar {
            background: #ffffff;
        }
    }

    & .navbar-toggle:focus, & .navbar-toggle:hover {
        background-color: $primaryColorDark;
    }

    @media (max-width: 767px) {
        & .navbar-nav .open .dropdown-menu>li>a {
            color: $primaryTextColor_dark;
        }
    }

    @media (prefers-color-scheme: dark) {
        .dropdown-menu { 
            background-color: $surface-color-dark;
            >li {
                >a {
                    color: #FFFFFF;
                    &:hover {
                        background: #181818;
                    }
                    &:focus {
                        background: #000000;
                    }
                }
            }
        }
    }
}

.navbar-brand	{
    padding: 8px;

    a>i {
        margin-right: 8px;
        color: $textColorOnPrimary;
    }
}

.card {
    background-color: #FFFFFF;
    border-radius: 2px;
    -webkit-box-shadow: 
        0px 3px 1px -2px rgba(0, 0, 0, 0.2), 
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 
        0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    box-shadow: 
        0px 3px 1px -2px rgba(0, 0, 0, 0.2), 
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 
        0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    
    padding: 12px;
    margin: 8px 0px;
    @media only screen and (max-device-width: 768px) {
        padding: 4px;
        margin: 8px 0px;
        small {
            font-size: 16px;
        }
    }
    @media (prefers-color-scheme: dark) {
        background-color: $surface-color-dark;
    }
    h1 {
        &.card-header {
            padding: 16px 20px 0px 20px;
            font-family: Roboto Mono, monospace;
            @media (prefers-color-scheme: dark) {
                small {
                    color: #FFFFFF;
                }
            }
        }
    }
    h3 {
        font-family: Roboto Mono, monospace;
    }
}

div {
    &.icon-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        >.workHistIcon {
            font-size: 35px;
            margin: 4px 8px;
        }
        >.skillIcon {
            font-size: 50px;
            margin: 4px 16px;
        }
    }
}

.profile_pic {
  	border-radius: 150px;
    margin: 20px;
    width: 200px;
    height: 200px;
    @media only screen and (max-device-width: 768px) {
        width: 100px;
        height: 100px;
        margin: 45px -8px 45px 0px;
    }
}

.profile_card {
  	margin: 20px 0px;
    h1 {
        font-size: 50px;
    }
    p {
        font-size: 16px;
    }
    @media only screen and (max-device-width: 768px) {
        margin: 12px 0px;
        h1 {
            font-size: 30px;
        }
        p {
            font-size: 12px;
        }
    }
}

.social a {
    margin: 4px 8px;
}

.workhist {
    h1 {
        padding: 16px 20px 0px 20px;
        font-family: Roboto Mono, monospace;
    }
    i {
        margin: 0px 16px;
        font-size: 35px;
    }
}

.classes {
    background-color:#ffffff;
    color:#000000;
}

.containclasses {
    >.col-xs-12 {
        >ul {
            >li {
                >p {
                    >span {
                        &.techUsed {
                            >i {
                                margin: 0px 8px;
                                font-size: 35px;
                            }
                        }
                    }
                }
            }
        }
    }
}

.bg-1 {
    color: #ffffff; /* White */
    text-shadow: 2px 2px 4px #000000; /*Black shadow*/
}

.bg-2 {
    background-color:#ffffff;
    color:#000000;
    border-radius: 0px 0px 5px 5px;
}

.bg2-b {
    background-color: #f8f8f8;
}

.footer {
    padding: 12px 20px;
    margin: 8px auto 0px auto;
    border-radius: 0px 2px 0px 2px;
    .col-xs-6{
        padding: 0px;
    }
}

#featProjImageContainer {
    img {
        height: 500px;
    }
}

/*div Styles*/
div	{
  	&.box {
        background-color:#000000; 
        color:#ffffff; 
        width:60%; 
        margin:0 auto; 
        padding:20px;
        opacity:0.9;
    }
    &.mid {
        width:60%; 
        margin:0 auto;
    }
}

/*Unordered List*/
ul {
  	&.a{
        list-style-type: square;
    }
}

@media (prefers-color-scheme: dark) {
    .devicon-apple-original, .devicon-apple-plain, .devicon-github-original-wordmark, .devicon-github-plain-wordmark, .devicon-github-original, .devicon-github-plain{
        &.colored {
            color: #FFFFFF;
        }
    }
}